@import "../../index.scss";

.log-button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 100px;
}

.log-button:hover {
  cursor: pointer;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  color: #83183d;
  border-radius: 5px;
}

.popup {
  position: fixed;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  // padding: 20px;
  height: 590px;
  width: 430px;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  // flex-direction: column;

  form {
    h1 {
      margin: -10px 0 0 0;
    }
    h5 {
      margin: 10px 0 30px 0;
      font-weight: 400;
    }
  }
  .x {
    height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  label {
    font-size: 14px;
    font-weight: 400;
  }

  input {
    border: none;
    border-bottom: 1px solid $textColor;
    height: 35px;
    width: 300px;
  }

  input:focus {
    outline: none;
  }

  .inputBlock {
    display: flex;
    padding: 10px;
    flex-direction: column;
  }

  .submitbutton {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    padding: 0;
    height: 40px;
    width: 100px;
    background-color: transparent;
    border: none;
    font-size: 18px;
    font-weight: 600;
  }

  .submitbutton:hover {
    cursor: pointer;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    color: #83183d;
    border-radius: 5px;
  }
}

.hide {
  display: none;
}

.show {
  display: flex;
}

.x:hover {
  cursor: pointer;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
