@import "../../index.scss";

.teamPopup {
  position: fixed;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 24px;
  height: 590px;
  width: 1100px;
  z-index: 999;
  display: grid;
  grid-template-rows: 48px 1fr 1fr;

  .teamHeader {
    display: grid;
    grid-template-columns: 1fr 32px;
    gap: 8px;
    justify-content: space-between;
    border-bottom: 2px solid #f7f7f7;

    h2 {
      margin: 0;
    }

    h4 {
      margin: 0;
    }
  }

  .teamListBlock {
    .inviteBlock {
      width: 200px;
      margin: 12px 0;
      transition: transform 0.1s ease-in-out;

      .inviteButton {
        display: grid;
        justify-content: center;
        align-items: center;
        grid-template-columns: 32px 1fr;
        height: 40px;
        width: 212px;
        background-color: $cerryColor;
        border: none;
        color: #f8f8f8;
        border-radius: 5px;
        font-size: 20px;
        transition: all 0.3s ease;
        padding-left: 5px;
        padding-right: 5px;

        h5 {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 36px;
          margin: 0;
        }
      }

      .inviteButton:hover {
        cursor: pointer;
        scale: 1.08;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
      }

      .inviteFormBlock {
        display: grid;
        height: 38px;
        grid-template-columns: 1fr 32px 32px;
        align-items: center;
        margin-right: 8px;
        gap: 8px;
      }

      .inviteForm {
        display: grid;
        grid-template-columns: 1fr 32px;
        height: 40px;
        gap: 12px;
      }

      .icons {
        height: 28px;
        width: 28px;
        justify-content: center;
        align-items: center;
      }

      .x {
        height: 32px;
        width: 32px;
        justify-content: center;
        align-items: center;
        align-self: center;
      }

      .invitationResponseMessage {
        position: fixed;
        z-index: 999;
        top: 15.5%;
        left: 50%;

        h4 {
          margin: 0;
        }
      }
    }

    .inviteBlock:hover {
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
      cursor: pointer;
    }

    .teamMemberCard {
      display: grid;
      grid-template-columns: 64px 1fr;
      gap: 12px;
      width: 320px;
      margin: 12px;

      h4 {
        margin: 0;
      }

      .memberContactInfoBlock {
        padding-top: 4px;
      }

      h6 {
        margin: 2px 0 0 0;
      }

      .memberIcon {
        height: 64px;
        width: 64px;
        background: rgb(243, 226, 213);
        border-radius: 32px;
        border: 2px solid #e0e0e0;
      }
    }
  }

  .teamMemberRequestContainer {
    display: grid;
    grid-template-columns: 300px 32px 32px;
    gap: 8px;

    .x {
      justify-self: end;
      height: 32px;
      width: 32px;
      justify-content: center;
      align-items: center;
      align-self: center;
    }
    .icons {
      justify-self: end;
      height: 32px;
      width: 32px;
      justify-content: center;
      align-items: center;
      align-self: center;
    }

    .icons:hover {
      cursor: pointer;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
    }
  }
}

.hide {
  display: none;
}
