@import "../../index.scss";
.project-list {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 180px;
  align-items: center;
  justify-content: center;

  border-bottom: 3px solid #f8f8f8;
  border-left: 3px solid #f8f8f8;
  background-color: white;
  height: 80px;
  padding: 0 4px 0 24px;
  // gap: 200px; /* Add this line to create space between columns */

  .input-fields {
    h4,
    h5 {
      height: 25px;
      margin: 0;
    }

    h4:nth-child(5) {
      display: grid;
      justify-content: center;
    }

    input {
      // display: block;
      height: 26px;
      padding: 0;
      margin: 0;
      height: 25px;
      // border: none;
      width: auto;
      // position: fixed;
    }
  }

  .input-fields:nth-child(3),
  .input-fields:nth-child(4),
  .input-fields:nth-child(5) {
    display: grid;
    justify-content: center;

    h4 {
      display: grid;
      justify-content: center;
    }
  }

  .show {
    display: flex;
    align-items: center;
  }

  .hide {
    display: none;
  }

  .addProjectButton {
    display: block;
    height: 40px;
    width: 120px;
    background-color: $cerryColor;
    border: none;
    color: #f8f8f8;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    transition: all 0.3s ease;
  }

  .addProjectButton:hover {
    cursor: pointer;
    scale: 1.08;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  }
}
