@import "../../index.scss";

.actionButton {
  display: grid;
  justify-content: center;
  align-items: center;
  min-width: 86px;
  height: 38px;
  font-size: 16px;
  background-color: $cerryColor;
  border-radius: 5px;
  border: none;
  color: #f8f8f8;
  transition: all 0.3s ease;
  font-weight: 600;
  padding: 0 15px 0 15px;

  .actionButtonName {
    margin: 0;
  }
}

.actionButton:hover {
  cursor: pointer;
  scale: 1.08;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.actionButton:active {
  cursor: pointer;
  background-color: $cerryColorLight;
}
