@import "../../index.scss";

.project-field {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 180px;
  justify-content: center;
  align-items: center;
  text-align: center;

  border-bottom: 3px solid #f8f8f8;
  border-left: 3px solid #f8f8f8;
  background-color: white;
  height: 40px;
  padding: 0 4px 0 24px;

  h5 {
    display: block;
    margin: 0px 0 0 0;
    /* text-align: center; */
  }

  .tasksButton {
    display: grid;
    justify-content: center;
    align-items: center;
    color: $cerryColor;
    transition: all 0.5s ease;
    border: 2px solid $cerryColor;
    border-radius: 5px;
    width: 156px;
    height: 28px;
  }

  .tasksButton:hover {
    cursor: pointer;
    background-color: $cerryColor;
    border: none;
    color: #f8f8f8;
    border-radius: 5px;
    scale: 1.08;
    // box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  }

  .tasksButton:active {
    cursor: pointer;
    background-color: $cerryColorLight;
  }

  .inputProjectField {
    border-bottom: none;
    font-size: 13px;
    font-weight: 700;
    width: 100%;
    text-align: center;
    color: red;
    // max-width: 100px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
  }

  input:nth-child(1),
  h5:nth-child(1) {
    text-align: left;
  }

  h5:nth-child(2) {
    transition: transform 0.1s ease-in-out;
    cursor: pointer;
  }

  h5:nth-child(2):hover {
    transform: scale(1.2);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }

  .buttonField {
    all: unset;
    width: 24px;
    // margin-right: 20px;
  }

  .projectFiledButtonsBlock {
    padding: 0 24px 0 24px;
    display: flex;
    justify-content: space-between;
  }

  .buttonField:hover {
    cursor: pointer;
    scale: 1.1;
  }
}

.project-field:hover {
  color: $cerryColor;
}
